.info-banner {
  .container {
    @media screen and (min-width: 521px) {
      display: flex;
      min-height: 500px;

      > * {
        flex: 1 0 50%;
        max-width: 50%;
      }
    }

    .image-container {
      position: relative;

      @media screen and (max-width: 520px) {
        height: 200px;
      }

      .image {
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        position: absolute;
        height: 100%;
        width: 50vw;

        @media screen and (max-width: 520px) {
          width: 100%;
        }
      }
    }

    .text-container {
      padding: 40px 40px 40px 0;
      box-sizing: border-box;
      color: #fff;
      align-self: center;

      @media screen and (max-width: 520px) {
        padding: 20px 0;
      }

      p {
        font-size: 18px;
        line-height: 26px;

        @media screen and (max-width: 520px) {
          font-size: 16px;
          line-height: 22px;
        }

        a {
          color: #fff;
        }
      }
    }
  }

  &.image-left {
    .text-container {
      order: 2;
      padding: 40px 0 40px 40px;

      @media screen and (max-width: 520px) {
        padding: 20px 0;
      }
    }

    .image-container {
      .image {
        right: 0;
      }
    }
  }

  &:last-of-type {
    @media screen and (max-width: 520px) {
      margin-bottom: 40px;
    }
  }
}

.main.light {
  .info-banner {
    .container {
      .text-container {
        color: #222;

        a {
          color: #222;
        }
      }
    }
  }
}